<template>
  <div id="app">
    <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div>
    <router-view />
  </div>
</template>
<script>

export default {

  created () {
    // this.init()
    // this.getFont(document, window)
  },
  methods: {
    init () {
      // var docEl = document.documentElement
      var resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize'
      var detect = this.$options.methods.detectOrient()
      if (!document.addEventListener) return
      window.addEventListener(resizeEvt, detect, false)
      document.addEventListener('DOMContentLoaded', detect, false)
    },
    detectOrient () {
      var cw = document.documentElement.clientWidth
      var _Width = 0
      var _Height = 0
      var sw = window.screen.width
      var sh = window.screen.height
      _Width = sw < sh ? sw : sh
      _Height = sw >= sh ? sw : sh

      if (cw === _Width) {
        // 竖屏
        this.setFont(true)
        return
      }
      if (cw === _Height) {
        // 横屏
        this.setFont(false)
      }
    },
    setFont (ori) {
      var docEl = document.documentElement
      var clientWidth = docEl.clientWidth
      if (!clientWidth) return

      // 横屏
      if (!ori) {
        if (clientWidth < 1334) {
          docEl.style.fontSize = 100 * (clientWidth / 1334) + 'px'
        } else {
          docEl.style.fontSize = '100px'
        }
      } else {
        // 竖屏
        if (clientWidth < 750) {
          docEl.style.fontSize = 100 * (clientWidth / 750) + 'px'
        } else {
          docEl.style.fontSize = '100px'
        }
      }
    },
    getFont (doc, win) {
      var user_webset_font = ''
      if (doc.documentElement.currentStyle) {
        user_webset_font = doc.documentElement.currentStyle['fontSize']
      } else {
        user_webset_font = getComputedStyle(doc.documentElement, false)['fontSize']
      }
      console.log(user_webset_font)
      var xs = parseFloat(user_webset_font) / 16
      console.log(xs)
      //      设置rem的js设置的字体大小
      var view_jsset_font, result_font
      var docEl = doc.documentElement
      var resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize'
      var clientWidth
      var recalc = function () {
        clientWidth = docEl.clientWidth
        if (!clientWidth) return
        if (!doc.addEventListener) return
        console.log(clientWidth)
        if (clientWidth < 750) {
          //              设置rem的js设置的字体大小
          view_jsset_font = 100 * (clientWidth / 750)
          //              最终的字体大小为rem字体/系数
          result_font = view_jsset_font / xs
          //              设置根字体大小
          docEl.style.fontSize = result_font + 'px'
          console.log(result_font)
        } else {
          docEl.style.fontSize = 16 + 'px'
        }
      }
      win.addEventListener(resizeEvt, recalc, false)
      doc.addEventListener('DOMContentLoaded', recalc, false)
    }

  }

}
</script>
